import {
  ChartBarSquareIcon,
  DocumentArrowDownIcon,
  PresentationChartBarIcon,
} from "@heroicons/react/24/outline";
import { Link } from "gatsby";
import * as React from "react";
import { HighLight } from "../../atoms";

const AbstractView = () => {
  const prefixPath = process.env.GATSBY_PREFIX_PATH_VALUE;
  const isDemo = process.env.GATSBY_DEMO === "true";
  return (
    <div className="mt-10 ">
      <div className="text-3xl   pt-4 mb-4 font-bold">
        {" "}
        LIDA: Automatic Generation of Grammar-Agnostic Visualizations and
        Infographics using Large Language Models
      </div>
      <div className="text-xl mb-4 mt-4 ">Abstract</div>
      <div className="text-base">
        Systems that support users in the automatic creation of visualizations
        must address several subtasks - understand the semantics of data,
        enumerate relevant visualization goals and generate visualization
        specifications. In this work, we{" "}
        <HighLight>
          <>
            pose visualization generation as a multi-stage generation problem{" "}
          </>
        </HighLight>{" "}
        and argue that well-orchestrated pipelines based on large language
        models (LLMs) and image generation models (IGMs) are suitable to
        addressing these tasks. We present LIDA, a novel tool for generating
        grammar-agnostic visualizations and infographics. LIDA comprises of 4
        modules - A SUMMARIZER that converts data into a rich but compact
        natural language summary, a GOAL EXPLORER that enumerates visualization
        goals given the data, a VISGENERATOR that generates, refines, executes
        and filters visualization code and an INFOGRAPHER module that yields
        data-faithful stylized graphics using IGMs. LIDA provides a python api,
        and a hybrid user interface (direct manipulation and{" "}
        <HighLight>multilingual</HighLight> natural language) for interactive
        chart, infographics and data story generation.
      </div>
      <div className="flex gap-4">
        <div className="my-4 mt-6 px-5 bg-accent p-3 inline-block rounded">
          <a
            target="_blank"
            rel="noopener noreferrer"
            className="inline-block text-white text-base duration-300 hover:text-white hover:brightness-90 "
            href={`${prefixPath}/files/paper.pdf`}
          >
            <DocumentArrowDownIcon className="h-5 w-5 inline-block" /> Read the
            paper
          </a>
        </div>

        <div className="my-4 mt-6 px-5 bg-accent p-3 inline-block rounded">
          <Link
            className="duration-300 text-white  hover:text-white hover:brightness-90 "
            to="/gallery"
          >
            {" "}
            <ChartBarSquareIcon className="h-5 w-5 inline-block mr-2" />
            Gallery{" "}
          </Link>
        </div>
        {isDemo && (
          <div className="my-4 mt-6 px-5 bg-accent p-3 inline-block rounded">
            <Link
              className="duration-300 text-white  hover:text-white hover:brightness-90 "
              to="/demo"
            >
              {" "}
              <PresentationChartBarIcon className="h-5 w-5 inline-block mr-2" />
              Try the Demo!{" "}
            </Link>
          </div>
        )}
      </div>
    </div>
  );
};
export default AbstractView;
